import React, { useEffect } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.scss";
import "./index.scss";
import { useDispatch, useSelector } from "react-redux";
import Slider from "react-slick";

// actions
import { getHomeProjectsApi } from "../../../stores/reducers/projectSlice";

// components
import Header from "../Layouts/NewHeader";

const settings = {
  dots: true,
  fade: true,
  infinite: true,
  speed: 500,
  autoplay: true,
  slidesToShow: 1,
  slidesToScroll: 1,
};

const NewHomeContent = (props) => {
  const dispatch = useDispatch();
  const { flagProjects } = useSelector((state) => state.projects);

  useEffect(() => {
    dispatch(getHomeProjectsApi({ offset: 0, limit: 20 }));
  }, [dispatch]);

  console.log("flagProjects: ", flagProjects);

  return (
    <div className="newHomeContent">
      <Header />
      <Slider className="silderHome" {...settings}>
        {flagProjects &&
          flagProjects.map((item, i) => (
            <div className="silderItem" key={i}>
              <img
                className="sliderImage"
                src={`${process.env.REACT_APP_CLOUD_FRONT_URI}${
                  item.picture && item.picture.key
                }`}
                alt=""
              />
            </div>
          ))}
      </Slider>
    </div>
  );
};

export default NewHomeContent;
